<template>
	<div id="chat">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">{{shopname}}</div>
      </div>
    </div>
    <div class="login_box text_center">
      <div  style="overflow: auto;margin-bottom: 50px;">
        <div v-for="(item,index) in chatList" :key="index">
          <div style="float: left;width: 100%;padding: 10px;" v-if="item.type==1">
            <img src="static/img/headImg.png" style="height: 30px;width: 30px;float: left;" />
            <img class="imgLeft" :src="item.img">
            <p class="liaoleftyou" v-if="item.content">{{item.content}}</p>
          </div>
          <div style="float: right;width: 100%;padding: 10px;" v-if="item.type==2">
            <img :src="userImg" style="height: 30px;width: 30px;float: right;" />
            <img class="imgright" :src="item.img">
            <p class="liaorightyou" v-if="item.content">{{item.content}}</p>
          </div>
        </div>
      </div>
      <div class="chat_bottom_bar">
        <input class="chat_input" type="text" v-model="content"/>
        <img src="static/img/icon_tianjia.png" class="chat_img_send" v-if="!content" @click="uploadImg"/>
        <button class="chat_send" v-if="content" @click="sendChat">发送</button>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'Job',
		data() {
			return {
        chatList:[],
        content:'',
        chatImg:'',
        userId:localStorage.getItem('userId'),
        userName:'',
        userImg:'',
        shopid:'',
        shopname:'',
        shoplogo: '',
        timer: null,
			}
		},
    mounted() {
		  this.shopid = this.$route.query.data.shopid;
      this.shopname = this.$route.query.data.shopname;
      this.shoplogo = this.$route.query.data.shoplogo;
      this.getChatList();
      this.getUserInfo();
    },
    created: function() {
      clearInterval(this.timer);
      this.timer = null;
      this.setTimer();
    },
    destroyed: function () {
      clearInterval(this.timer);
      this.timer = null
    },
		methods:{
      setTimer() {
        if(this.timer == null) {
          this.timer = setInterval( () => {
            this.getChatList();
          }, 3000);
        }
      },
      uploadImg(){
        let $this = this;
        dsBridge.call('uploadHeadImg');
        dsBridge.register('addHeadImgCallback', function(rpdata) {
          $this.chatImg = rpdata;
          $this.sendChat();
        })
      },
      sendChat(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          shopid:'1',
          type:'2',
          content:$this.content,
          img:$this.chatImg,
        };
        let qs = require('qs');
        $this.$ajax.post("chat/addchat", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.chatList.push({
              content:$this.content,
              img:$this.chatImg,
              type:'2',
            });
            $this.content = '';
            $this.chatImg = '';
          }
        })
        .catch(function(err) {})
      },
      getChatList(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          shopid:'1'
        };
        let qs = require('qs');
        $this.$ajax.post("chat/getchatlist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.chatList = rpdata.data.data;
            console.log('获取了一次聊天记录');
          }
        })
        .catch(function(err) {})
      },
      getUserInfo(){
        let $this = this;
        let obj = {
          userid: $this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("user/getuserinfo", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.userName = rpdata.data.data.username;
            $this.userImg = rpdata.data.data.userimg;
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .imgLeft {
    float: left;
    margin-left: 0.4rem;
    padding: 0.28rem 0.4rem;
    max-width: 12.1rem;
  }

  .imgright {
    float: right;
    margin-right: 0.4rem;
    padding: 0.28rem 0.4rem;
    max-width: 12.1rem;
  }
  .liaorightyou {
    padding: 0.28rem 0.4rem;
    background-color: #ffffff;
    margin-right: 0.4rem;
    border-radius: 0.1rem;
    font-size: 0.3rem;
    max-width: 12.1rem;
    float: right;
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
  }
  .liaoleftyou {
    padding: 0.28rem 0.4rem;
    background-color: #ffffff;
    margin-left: 0.4rem;
    border-radius: 0.1rem;
    font-size: 0.3rem;
    max-width: 12.1rem;
    float: left;
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
  }
  .login_box{
    margin-top: 50px;
  }
  .chat_input{
    height: 20px;
    margin-left: 10%;
    width: 66%;
    border: 0;
    margin-right: 5px;
    border-radius: 3px;
    font-size: 12px;
  }
  .chat_bottom_bar{
    display: flex;
    align-items: center;
    height: 50px;
    position: fixed;
    width: 100%;
    bottom: 0;
    border-top: 1px solid rgb(216, 203, 203);
    background-color: #f4f4f4;
  }
  .chat_send{
    font-size: 12px;
    padding: 5px;
    width: 16%;
    border-radius: 5px;
    border: 0;
    background-color: #78af42;
    color: #ffffff;
  }
  .chat_img_send{
    width: 20px;
    vertical-align: middle;
  }
</style>



